.header {
    margin-top: 60px;
}

.headerTitles {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Lora', 'serif';
    color: #444;
}

.headerTitleSm {
    position: absolute;
    top: 18%;
    font-size: 25px;
}

.headerTitleLg {
    position: absolute;
    top: 20%;
    font-size: 90px;
}

.headerImg {
    width: 100%;
    height: 450px;
    margin-top: 60px;
    object-fit: cover;
}